<template>
  <div id="login">
    <van-nav-bar
      title=""
      left-text=""
      right-text=""
      left-arrow
      @click-right="onClickRight"
      @click-left="onClickLight"
    />
    <van-cell-group class="cell-group">
      <van-field label="" v-model="username" placeholder=""/>
      <van-field label="" type="password" v-model="password" placeholder=""/>
    </van-cell-group>

    <van-button id="submit" size="large" @click="submit"></van-button>
  </div>
</template>

<script>
export default {
  name: 'Login',
  data () {
    return {
      username: '',
      // email: '',
      password: ''
    }
  },
  methods: {
    submit () {
      var _this = this
      this.axios({
        method: 'post',
        url: '/user/login',
        data: {
          user: {
            username: _this.username,
            password: _this.password
          }
        }
      }).then((response) => {
        console.log(response)
        let data = response.data
        if (data.userid) {
          //console.log(_this.$route.query);
          // let redirectPath = _this.$route.query.redirect || '/'
          let redirectPath = '/'
          localStorage.isLogin = true
          this.$store.commit('isLogin')
          sessionStorage.userid = data.userid
          this.$router.push({path: redirectPath})
        } else {
          alert(data.error)
        }
      })
    },
    onClickRight () {
      this.$router.push({path: '/register'})
    },
    onClickLight () {
      window.history.go(-1)
    }
  }
}
</script>

<style scoped>
  .cell-group {
    margin-top: 0.3rem;
  }
  #submit {
    margin-top: 1rem;
  }
</style>
